.audioPlayer{
    width: 500px;
    height: 200px;
    position: relative;
    background: white;
    display: flex;
    flex-direction: column;
}
.audioPlayer::before{
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    width: calc(100% + 6px);
    height: calc(100% + 6px);
    background: linear-gradient(180deg, rgba(188,156,255,1) 0%, rgba(160,182,255,1) 100%);
    z-index: -1;
}
.audioPlayer__svg{
    width: 50px;
}
.audioPlayer__svg:hover{
    cursor: pointer;
}
.audioPlayer__volume{
    width: 50px;
    position: absolute;
    bottom: 30px;
    right: 30px;
}
.playerHandler{
    position: absolute;
    bottom: 30px;
    align-self: center;
}
