
/* videoPlayer */

.videoPlayer{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 1280px;
    height: 790px;
}
.videoPlayer_isFullScreen{
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
}
.videoPlayer_isFullScreen + .controls{
    background-color: white;
    position: absolute;
    bottom: 20px;
    width: 60vw;
}
.videoPlayer__videoContainer{
    width: 100%;
    border: 2px solid black;
    border-bottom: 0px;
    box-sizing: border-box;
}

/* controls */

.controls{
    height: 70px;
    width: 100%;
    border: 2px solid black;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
}
.controls__playingHandler{
    width: 60px;
    margin-left: 30px;
}
.controls__volume{
    margin-right: 30px;
    width: 60px;
    transform: translate(0, 1px);
}
.controls__fullScreenHandler{
    width: 60px;
    margin-right: 30px;
}