.progressBar{
    display: grid;
    grid-template-columns: 15% 70% 15%;
    height: 100px;
    width: 100%;
}
.progressBar__range{ 
    height: 5px;
    width: 100%;
    -webkit-appearance: none;
    background: #a0b6ff;
}
.progressBar__range:hover{
    cursor: pointer;
}
.progressBar__range::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 15px;
    border-radius: 6px;
    background: #bc9cff;
    cursor: pointer;
    border: none;
}
.progressBar__label{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
}
.progressBar__div{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}