.volume__svg{
    width: 100%;
}
.volumeChanger{
    position: absolute;
    bottom: -3px;
    right: -70px;
    width: 50px;
    height: 200px;
    border: 3px solid black;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.volumeChanger__range{
    -webkit-appearance: none;
    transform: rotate(-90deg) translate(-20px, 0px);
    background: black;
    height: 2px;
}
.volumeChanger__range:hover{
    cursor: pointer;
}
.volumeChanger__range::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 3px;
    background: black;
    cursor: pointer;
    border: none;
}
.volumeChanger__label{
    position: absolute;
    top: 12px;
    font-size: 1.2em;
    font-weight: 500;
}
